import React from 'react';
import { useCookies } from 'react-cookie';
import { useNavigate } from 'react-router-dom';
import { authService } from './../../services/auth.service'
import './Login.scss';

const Login = () => {
  // eslint-disable-next-line
  const [cookies, setCookie, removeCookie] = useCookies(['token']);
  const navigate = useNavigate();
  const loginRef = React.createRef();
  const passRef = React.createRef();

  const loginFn = (e) => {
    e.preventDefault();

    const login = loginRef.current.value;
    const password = passRef.current.value;

    const data = { login, password };

    authService.login(data)
    .then((res) => {
      document.querySelector('p').inerHTML = ('')
      if (res.data.success) {
        setCookie('token', res.data.token, { path: '/' , maxAge: 3600 });
        navigate("/home")
      }
      else {
        document.querySelector('p').innerHTML = ('Zły login albo hasło');
      };
    });
  }
  return (
    <div className="login-container">
        <h1 className="">Login</h1>
        <form className="login-form">
            <input ref={loginRef} placeholder="Username" type="text"></input>
            <input ref={passRef} placeholder="Password" type="password"></input>

            <div className="button-container">
            <button onClick={e => loginFn(e)} className="login-button">Zaloguj</button>
            </div>
            <p></p>
        </form>
    </div>
  );
};

export default Login;