import React from 'react';
import { Link } from 'react-router-dom'
import './Menu.scss';

const Menu = () => {
    return(
        <div className="menu-container">
            <ul className="menu">
                <li className="link">
                    <Link className="link_text" to="/home">Home</Link>
                </li>
                <li className="link">
                    <Link className="link_text" to="/home/classes">Klasy</Link>
                </li>
                <li className="link">
                    <Link className="link_text" to="/home/plans">Plany</Link>
                </li>
                <li className="link">
                    <Link className="link_text" to="/home/subjects">Przedmioty</Link>
                </li>
            </ul>

        </div>
    );
};

export default Menu;