import React, { useState, useEffect} from 'react';
import Header from './../header';
import Menu from './../menu';
import './Subjects.scss';
import { subjectServices } from './../../services/subjects.service';

const Subjects = () => {
    const [subjects, setSubjects ] = useState([]);

    useEffect(() => {
        subjectServices.subjects()
        .then(res => {
            setSubjects(res.data.subjects);
        });
    },[setSubjects]);

    return(
        <div className="subjects-container">
            <Header></Header>
            <Menu></Menu>
            <div className="content">
            <h1>Dodaj przedmiot</h1>
            <div>
                <input type="text" placeholder="Nazwa przedmiotu" />
                <button>Dodaj</button>
            </div>
            <div>
                <ul>
                    {subjects.map((subjectName, index) => {
                        return <li kay={index}>{subjectName}</li>
                    })}
                </ul>
            </div>
            

            </div>
        </div>
    );
};

export default Subjects